/* App CSS */
.App {
  text-align: center;
}

.jumbotron.jumbotron {
  width: 100%;
  height: 680px;
  background: url(./images/journey_.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  margin: 0px;
}
.jumbo-text {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.jumbo-text span {
  text-transform: uppercase;
  display: block;
}
.text1 {
  color: white;
  font-size: 60px;
  font-weight: 700;
  letter-spacing: 8px;
  margin-bottom: -10px;
  position: relative;
  animation: text 3s 1;
}
.text2 {
  color: white;
  font-size: 30px;
  font-weight: 300;
}
@keyframes text {
  0% {
    color: black;
    margin-bottom: -40px;
  }
  30% {
    letter-spacing: 25px;
    margin-bottom: -40px;
  }
  85% {
    letter-spacing: 8px;
    margin-bottom: -40px;
  }
}

.navbar {
  width: 100%;
  position: absolute !important;
  bottom: 0;
  left: 0;
}
.mr-auto {
  margin-left: 100px;
}
a.nav.nav-link {
  margin-right: 40px;
  color: white !important;
}
a.nav.nav-link:hover {              /*나중에 transition 적용하기 */
  font-size: larger;
  font-weight: bold;
}

/* About, Skills, Projects, Contact CSS */
.title {
  font-size: 3em;
  font-weight: 600;
  color: black;
  padding: 90px 90px 50px;
}
div.col-md-6 {
  margin-bottom: 40px;
}
p.subtitle {
  font-size: 1.6em;
  font-weight: 500;
  padding: 35px 0px 45px;
  letter-spacing: 1.5px;
}
p.subtitle span {
  font-size: small;
}

/* Skills CSS */
#skills, #contact {
  background-color: #f5f5f5;
}
table {
  width: 85%;
  margin: 0px auto;
}
.skill-img {
  width: 150xp;
  height: 150px;
  margin: 50px;
  object-fit: contain;
  border: 1px solid gray;
  display: inline;
}

/* Projects CSS */
.flex-box {
  width: 85%;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 0px !important;
}

.project-card {
  width: 33.3%;
  height: 300px;
  position: relative;
  border: 1px solid #dcdcdc;
}

.project-img {
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: 0.5s ease;
}
.project-card:hover .project-img {
  opacity: 0.2;
} 

.text-area {
  text-align: center;
  position: absolute;
  top: 20%;
  left: 30%;
  opacity: 0;
  transition: 0.5s ease;
  transform: translate(-3%, -3%);
}
.text-area h4 {
  color: black;
  padding: 10px 5px;
}
.text-area p {
  font-weight: 600;
  padding: 20px 5px;
}
.project-card:hover .text-area {
  opacity: 1;
}





